<template>
  <div class="typeaheadsearch">
    <div class="search-container">

      <label for="search-no-fill">Search: </label>
      <input
        type="text"
        id="search-no-fill"
        autocomplete="do-not-autofill"
        class="searchField"
        v-model="searchString"
        @keyup="handleKeyUp"
      />
    </div>
    <div class="results" v-if="false">
      <div v-for="(result, index) in results" :key="index" class="result-item" @click="handleClick(result)" >
        {{ result.place_name }}
      </div>
    </div>

    <div class="results-container">
    <type-ahead-results  v-show="results.length > 0" :style="resultsStyle"  />
    </div>
  </div>
</template>

<script>
import apiService from "../service/ApiService";
import TypeAheadResults from './TypeAheadResults.vue';
export default {
  name: "type-ahead-search",
  components: {
    TypeAheadResults
  },
  data() {
    return {
      searchString: "",
      waiting: false,
    //   resulstVisible: 'hidden',
    //   resultsOpactiy: 0,
    //   resultsDisplay: 'none'
    };
  },
  computed: {
    searchText() {
        return this.$store.state.searchString;
    },
    bbox() {
      return this.$store.state.mapBounds;
    },
    results() {
      return this.$store.state.typeAheadResults;
    },
    resultsVisible() {
        return this.searchText != '' && this.results.length > 0;
    },
    resultsStyle() {

        return {
            visibility: this.resultsVisible ? 'visible' : 'hidden',
  opacity: this.resultsVisible ? 1 : 0,
  position: 'absolute',
  transition: 'all 0.5s ease',
  'margin-top': 0,
  left: '100px',
  display: this.resultsVisible ? 'block' : 'none',
  'flex-direction': 'column',
  'align-content': 'stretch',
  'white-space': 'nowrap',
    // width: '100%',
  overflow: 'auto'
  // overflow: 'visible'

        }
    }
  },
  methods: {
    handleKeyUp() {
      //   this.results.push(this.searchString)
      this.$store.commit('UPDATE_SEARCH_STRING', this.searchString);
      if (!this.waiting) {
          this.waiting = true;
        apiService.mapBoxSearchBBox(this.searchText, apiService.getBboxString(this.bbox)).then((resp) => {
          let results = [];
          if (resp && resp.data && resp.data.features) {
            const places = resp.data.features;
            places.forEach((place) => {
              results.push(place);
            });
            
            this.updateTypeAheadResults(results);
            this.waiting = false;
          }
        });
      }
    },
    updateTypeAheadResults(results) {
      this.$store.commit('UPDATE_TYPE_AHEAD_RESULTS', results);
    },
  },
  watch: {
    searchText(newText, oldText) {
      if (newText != oldText && newText == '') {
        this.searchString = '';
      }
  }
}
}
</script>

<style>

.typeaheadsearch {
    text-align: left;
    /* width: 400px; */
    z-index: 900;
}

.search-container {
  width: 100%;

  padding: 10px 10px 5px 10;
}

.search {
}

.results {
  /* height: 400px; */
  position: absolute;
  background-color: white;
  color: #00adee;
  padding: 10px;
  margin-left: 40px;
  /* white-space: nowrap; */
  box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
  /* width: 100%; */

width: 300px;
  overflow-x: auto;
        overflow-y: hidden;
        white-space: nowrap;


}

.result-item {
  padding: 15px;
  width: 100%;
  /* flex-grow: 1; */
  padding: 10px;
  /* margin-left: 140px; */
  position: relative;
}
/* .result-item:hover {
    background: lightgray;
    width: 100%;
    cursor: pointer;
} */


.results-container {
  width: 300px;
  /* height: auto; */
  overflow: auto;
  display: flex;
  justify-items: stretch;
}

.scroll {
        margin: 4px, 4px;
        padding: 15px  10px;
        width: 300px;
        overflow-x: auto;
        overflow-y: hidden;
        white-space: nowrap;
    }
</style>