import axios from 'axios';

export default {

    addressSearch(searchString, limit) {
        return axios.get(`https://nominatim.openstreetmap.org/?addressdetails=1&q=${searchString}&format=json&limit=${limit}`);
    },
    reverseLookup(coords) {
        return axios.get(`https://nominatim.openstreetmap.org/reverse?lat=${coords.lat}&lon=${coords.lng}&format=json`)
    },
    mapBoxFindAddress(address) {
        return axios.get(`https://api.mapbox.com/geocoding/v5/mapbox.places/${address}.json?&access_token=pk.eyJ1IjoieW9hdi1tLXRlIiwiYSI6ImNsMGN5bGoxbDAyb2wza2xjaW81MWszcXoifQ.go165gRzkDJg4-GaoXCm3Q`)
    },
    mapBoxSearchBBox(searchString, bboxString) {
        return axios.get(`https://api.mapbox.com/geocoding/v5/mapbox.places/${searchString}.json?bbox=${bboxString}&access_token=pk.eyJ1IjoieW9hdi1tLXRlIiwiYSI6ImNsMGN5bGoxbDAyb2wza2xjaW81MWszcXoifQ.go165gRzkDJg4-GaoXCm3Q`)
    },
    mapBoxSearch(searchString, proximity) {
        return axios.get(`https://api.mapbox.com/geocoding/v5/mapbox.places/${searchString}.json?proximity=${proximity}&access_token=pk.eyJ1IjoieW9hdi1tLXRlIiwiYSI6ImNsMGN5bGoxbDAyb2wza2xjaW81MWszcXoifQ.go165gRzkDJg4-GaoXCm3Q`);
    },
    getBboxString(bounds) {
      const ne = bounds._northEast;
      const se = bounds._southWest;
      const bboxString = `${se.lng},${se.lat},${ne.lng},${ne.lat}`;
        return bboxString;
    }

}