<template>
  <div id="container" @click="handleMapClick">
    <h1>My Map Demo</h1>
    <div class="search">
      <!-- <label for="search-box">Search: </label>
      <input id="search-box" type="text" v-model="searchString" /> -->
      <!-- <type-ahead-search class="type-ahead-search-box" /> -->
      <type-ahead />
      <div class="search-btns">
      <button class="searchBtn" @click="doSearch">Search</button>
      <button class="searchBtn" @click="doGo">Go</button>
      <button class="searchBtn" @click="centerMap">Origin</button>
      <button class="searchBtn" @click="clear">Clear</button>
      </div>
    </div>

    <div class="map-section">
      <!-- Map container -->
      <div
        id="mapContainer"
        ref="theMap"
        @contextmenu.prevent.stop="handleContextMenu($event)"

      >
        <context-menu
          :display="showContextMenu"
          :choices="choices"
          ref="menu"
          class="context-menu"
        >
        </context-menu>
      </div>
    </div>
  </div>
</template>

<script>
// Leaflet imports
import "leaflet/dist/leaflet.css";
import L from "leaflet";

// Service import
import apiService from "../service/ApiService";

import ContextMenu from "../components/ContextMenu";
// import TypeAheadSearch from '../components/TypeAheadSearch.vue';

import TypeAhead from './TypeAhead.vue';

export default {
  name: "map-demo",
  components: {
    ContextMenu,
    // TypeAheadSearch
    TypeAhead
  },
  data() {
    return {
      searchString: "",
      showContextMenu: false,
      map: {},
      mapCenter: [39.9552836, -75.182144],
      startCity: 'Philadelphia',
    //   startSearch: "Duane Morris Plaza",
    
      startSearch: '30 S 17th Street',
      zoom: 13,
      // create Leaflet icon from image - need require to force image to be available
      markerIcon: L.icon({
        iconUrl: require("../assets/marker-blue.png"),
        iconSize: [30, 30],
      }),
      choices: ["Add Pin", "Cancel"],
      markerChoices: ["Remove Pin", "Cancel"],
      savedEvent: "",
      savedMarkerEvent: "",
      goPoint: "",
      proximity: "",
    };
  },
  mounted() {
    // wait for component to be mounted, then create the map
    this.setupMapBoxLeafletMap();
    this.initMap();
    // this.proximity = [this.mapCenter[1], this.mapCenter[0]];
    // this.search(this.startSearch, 5);
    // this.mapBoxSearch(this.startSearch);
  },
  computed: {
    choice() {
      return this.$store.state.contextSelection;
    },
    newMarker() {
      return this.$store.state.newMarker;
    },
    showTypeAheadResults() {
      return this.$store.state.typeAheadResults.length > 0;
    },
    searchText() {
      return this.$store.state.searchString;
    }
    
  },
  methods: {
    setupMapBoxLeafletMap() {
      const accessToken =
        "pk.eyJ1IjoieW9hdi1tLXRlIiwiYSI6ImNsMGN5bGoxbDAyb2wza2xjaW81MWszcXoifQ.go165gRzkDJg4-GaoXCm3Q";

      let mapboxTiles = L.tileLayer(
        "https://api.mapbox.com/styles/v1/mapbox/streets-v11/tiles/{z}/{x}/{y}?access_token=" +
          accessToken,
        {
          attribution:
            '© <a href="https://www.mapbox.com/feedback/">Mapbox</a> © <a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a>',
          tileSize: 512,
          zoomOffset: -1,
        }
      );

      this.map = L.map("mapContainer", {
        center: this.mapCenter,
        zoom: this.zoom,
      }).addLayer(mapboxTiles);


      const handleDragEndFunc = this.handleDragEnd;

      this.map.on('moveend',function(){
        handleDragEndFunc();
      });
    },
    initMap() {
        apiService.mapBoxFindAddress(this.startCity).then( resp => {
            if (resp.data.features.length > 0) {
                const place = resp.data.features.find( feature => feature.place_type == 'place');
                if (place) {
                    const bbox = place.bbox;
                    const bboxString = `${bbox[0]},${bbox[1]},${bbox[2]},${bbox[3]}`;
                    apiService.mapBoxSearchBBox(this.startSearch, bboxString).then( resp => {
                        if (resp.data.features.length > 0) {
                            const place = resp.data.features[0];
                            const center = place.center;
                            this.mapCenter = [center[1],center[0]];
                            this.proximity = [center[0],center[1]];
                            this.map.setView(this.mapCenter, this.zoom);
                            const coords = { lng: center[0], lat: center[1]};
                            this.createMarker(coords, place.place_name);
                        }
                    })
                }
                this.updateMapBounds();
            }
        })
    },
    updateMapBounds() {
      this.$store.commit('UPDATE_MAP_BOUNDS', this.map.getBounds());
    },
    doSearch() {
      // this.mapBoxSearch(this.searchText);
      this.mapBoxSearchBbox(this.searchText);
      // this.searchString = "";
      this.$store.commit('UPDATE_SEARCH_STRING', '');
    },
    doGo() {
      this.go(this.searchText, 20);
      // this.searchString = "";
      this.$store.commit('UPDATE_SEARCH_STRING', '');
    },
    go(searchString) {
      apiService.mapBoxFindAddress(searchString).then((resp) => {
        const features = resp.data.features;
        if (features.length > 1) {
            const place = features[0];
          const coords = {
            lng: place.center[0],
            lat: place.center[1],
          };
          this.createMarker(coords, place.place_name);
          this.map.setView([coords.lat, coords.lng], this.zoom);
          this.proximity = [coords.lng, coords.lat];
          this.updateMapBounds();
        }
      });
    },
    addMarker(searchResult) {
      if (searchResult.length > 0) {
        // iterate through all results
        for (let i = 0; i < searchResult.length; i++) {
          const place = searchResult[i];
          // this is just to filter out non building locations (just added this for Duane Morris Plaza - may be a better way)
          if (place.type != "pedestrian") {
            // extract coordinates from data and create coordinates object
            const coords = { lng: place.lon, lat: place.lat };
            // pass coordinates and text for marker
            this.createMarker(coords, place.display_name);
          }
        }
      }
    },
    createMarker(coords, displayText) {
      if (!this.markerExists(coords)) {
        // create a Leaflet marker from coords and displayText using the marker icon
        const marker = L.marker(
          { lon: coords.lng, lat: coords.lat },
          { icon: this.markerIcon }
        )
          .bindPopup(displayText) // bind text to marker popup
          // these are for showing popup on hover in addition to click (remove if not wanted)
          .on("contextmenu", (event) => this.handleMarkerClick(event))
          .on("mouseover", function () {
            this.openPopup();
          })
          .on("mouseout", function () {
            this.closePopup();
          })
          .addTo(this.map); // add marker to map
        this.$store.commit("ADD_MARKER", marker);
      }
    },
    getCoordsFromClick(event) {
      return this.map.mouseEventToLatLng(event);
    },
    createMarkerFromCoords(coords) {
      apiService.reverseLookup(coords).then((resp) => {
        const info = resp.data.address;
        const displayText = this.getInfoFromReverseLookupData(info);
        this.createMarker(coords, displayText);
      });
    },
    getInfoFromReverseLookupData(info) {
      const text = `${info.house_number ? info.house_number : ""} ${
        info.road
      }, ${info.city} ${info.state} ${info.postcode}, ${info.country}`;
      return text;
    },
    // recenter, reset zoom factor
    centerMap() {
      this.map.setView(this.mapCenter, this.zoom);
    },
    handleContextMenu(event) {
      // make sure this came from map
      if (event.target == event.currentTarget) {
        this.savedEvent = event;
        this.$refs.menu.open(
          event,
          this.map.mouseEventToContainerPoint(event),
          this.choices
        );
      }
    },
    handleMapClick() {
      this.$refs.menu.close();
      this.savedEvent = {};
      this.$store.commit('UPDATE_TYPE_AHEAD_RESULTS', []);
    },
    handleMarkerClick(event) {
      this.savedMarkerEvent = event;
      this.$refs.menu.open(
        event,
        this.map.mouseEventToContainerPoint(event.originalEvent),
        this.markerChoices
      );
    },
    removeMarker() {
      const id = this.savedMarkerEvent.target._leaflet_id;
      const marker = this.$store.state.markers.find(
        (curMarker) => curMarker._leaflet_id == id
      );
      marker.closePopup().unbindPopup();
      marker.remove();
      this.$store.commit("REMOVE_MARKER", id);
      this.savedMarkerEvent = "";
    },
    clear() {
      this.$store.state.markers.forEach((marker) => {
        marker.closePopup().unbindPopup();
        marker.remove();
      });
      this.$store.commit("REMOVE_ALL_MARKERS");
      this.centerMap();
      this.search(this.startSearch, 5);
    },
    markerExists(coords) {
      let exists = false;
      this.$store.state.markers.forEach((marker) => {
        const point = marker._latlng;
        if (coords.lng == point.lng && coords.lat == point.lat) {
          exists = true;
        }
      });
      return exists;
    },
    mapBoxSearch(searchString) {
      const proximityString = `${this.proximity[0]},${this.proximity[1]}`;
      apiService.mapBoxSearch(searchString, proximityString).then((resp) => {
        const features = resp.data.features;
        features.forEach((place) => {
          const coords = {
            lng: place.geometry.coordinates[0],
            lat: place.geometry.coordinates[1],
          };
          this.createMarker(coords, place.place_name);
        });
      });
    },
    mapBoxSearchBbox(searchString) {
      const bounds =this.map.getBounds();
      const bBoxString = apiService.getBboxString(bounds)
      apiService.mapBoxSearchBBox(searchString, bBoxString).then((resp) => {
        const features = resp.data.features;
        features.forEach((place) => {
          const coords = {
            lng: place.geometry.coordinates[0],
            lat: place.geometry.coordinates[1],
          };
          this.createMarker(coords, place.place_name);
        });
      });
    },
    handleDragEnd() {
      const center = this.map.getCenter();
      this.proximity = [center.lng, center.lat];
      this.updateMapBounds();
    }
  },
  watch: {
    // whenever choice changes, this function will run
    choice(newChoice, oldChoice) {
      if (newChoice != oldChoice) {
        if (newChoice === "Add Pin") {
          if (this.savedEvent != "") {
            const coords = this.getCoordsFromClick(this.savedEvent);
            this.createMarkerFromCoords(coords);
          }
        } else if (newChoice === "Remove Pin" && this.savedMarkerEvent != "") {
          this.removeMarker();
        }
      }
    },
    newMarker(newChoice, oldChoice) {
      if (newChoice && newChoice != oldChoice && newChoice != '') {
        const coords = {
            lng: newChoice.geometry.coordinates[0],
            lat: newChoice.geometry.coordinates[1],
          };
        this.createMarker(coords, newChoice.place_name);

      }
    }

  },
}
</script>

<style scoped>
#mapContainer {
  width: 90vw;
  height: 100vh;
  margin: 10px auto;

  border: 5px solid #00adee;
}

.search {
  font-size: 20px;
  color: #ffffff;
  background-color: #00adee;
  padding: 20px 5px;
  width: 90vw;
  margin: 0 auto;
  display: flex;
  padding-left: 10px;
}

.map-section {
  position: relative;
  margin-top: 20px;
}

.search-btns {
  justify-self: flex-end;
  flex-grow: 1;
  display: flex;
  justify-content: flex-end;
  padding-right: 10px;
}
.searchBtn {
  color: #ffffff;
  background-color: #00adee;
  border: 1px solid #ffffff;
  margin-left: 10px;
}

.searchBtn:hover {
  color: #00adee;
  background-color: #ffffff;
}

.context-menu {
  position: relative;
  width: 100px;
}

.type-ahead-search-box {
  display: inline;
}
</style>