<template>
  <div class="results" v-if="results.length > 0" :style="style" >
      <div v-for="(result, index) in results" :key="index" class="result-item" @click="handleClick(result)" :id="'cell-' + index" @mouseover="setMouseOverCell($event)">
        <span class="cell" :class="{ highlight: 'cell-' + index == mouseOverCell }" >{{ result.place_name }}</span>
      </div>
    </div>
</template>

<script>
export default {
    name: 'type-ahead-results',
    data() {
    return {
      left: 10, // left position
      top: 10, // top position
      show: false, // affect display of context menu,
      mouseOverCell: ''
    };
  },
  computed: {
    // get position of context menu
    style() {
      return {
        // top: this.top + "px",
        // left: this.left + "px",
      };
    },
    results() {
        return this.$store.state.typeAheadResults;
    }
  },
  methods: {

    handleFieldFocus(event) {
      event.prevent.stop;
    },
    handleClick(place) {
      this.$store.commit('ADD_MARKER_TO_MAP', place)
      this.clear();
    },
    clear() {
      this.$store.commit('UPDATE_SEARCH_STRING', '');
      this.$store.commit('UPDATE_TYPE_AHEAD_RESULTS', []);
    },
    setMouseOverCell(event) {
      event.stop;
      const cellString = event.target.id;
      // const width = event.target.offsetWidth;
      // const height = event.target.offsetHeight;
      // console.log(`${event.target.classList[0]}  / ${event.currentTarget.classList[0]} w: ${width} h: ${height}`);
      const bounds = event.target.getBoundingClientRect();
      console.log(bounds)
      this.mouseOverCell = cellString;
    }
  }
  

}
</script>

<style>
/* Styles are in type ahead class that contains this */

.cell {
  width: 100%;
    cursor: pointer;
    padding: 10px;
    margin-left: -20px;
}

.highlight {
  background: lightgray;
    /* width: 100%;
    cursor: pointer;
    padding: 10px; */
}
</style>